.reproductive {
	padding-top: 50px;
	padding-bottom: 50px;
}

.reproductive__inner {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.reproductive__title {
	max-width: 820px;
	font-size: clamp(24px, 3.5vw, 40px);
	line-height: 1.29;
	text-align: center;
	color: #333333;
	margin-bottom: clamp(25px, 4.5vw, 50px);
}

.reproductive__img {
	width: 160px;
	height: 160px;
	margin-bottom: 25px;
	margin: 0 auto;
}

@media (max-width: 978px) {
	.reproductive {
		padding-top: 25px;
		padding-bottom: 25px;
	}
	.reproductive__img {
		width: 100px;
		height: 100px;
	}
}
