.program {
	padding-top: 50px;
	padding-bottom: 50px;
}

.program__wrapper {
	position: relative;
	z-index: 1;
	&::before {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		content: "";
		width: 50%;
		background-color: #fff;
	}
	&::after {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		content: "";
		width: 50%;
		border-top-left-radius: 50px;
		border-bottom-left-radius: 50px;
		background-color: #f1eafd;
	}
}

.program__inner {
	position: relative;
	z-index: 41;
	background-color: transparent;
}

.program__grid {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 40px;
}
.column-left__wrapper {
	max-width: 440px;
}
.program__column-left {
	margin-top: 30px;
	margin-bottom: 30px;
}
.program__column-right {
	margin-top: 30px;
	margin-bottom: 30px;
	margin-left: 25px;
}

.program__title {
	font-weight: 600;
	font-size: clamp(24px, 3.5vw, 40px);
	line-height: 1.29;
	text-align: left;
	color: #f178b6;
	margin-bottom: 24px;
}

.program__column-left .program__title {
	max-width: 390px;
}

.program__subtitle {
	margin-bottom: 18px;
	font-weight: 700;
	font-size: clamp(18px, 3.5vw, 24px);
	line-height: 1.3;
	text-align: left;
	color: #333333;
}

.list-action__item {
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 22px;
	line-height: 1.5;
	text-align: left;
	color: #333333;
}

.promo-group__title {
	font-size: 16px;
	color: #f56ca8;
	margin-bottom: 4px;
}

.program__column-left .program-list-action {
	margin-bottom: 56px;
}

.program-list-action {
	list-style-type: disc;
	padding-left: 15px;
}
.program-list-action__item {
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 22px;
	line-height: 1.5;
	text-align: left;
	color: #333333;
}

.program-list-action__item:not(:last-child) {
	margin-bottom: 10px;
}

.program__column-right .program__title {
	max-width: 440px;
}

.program__column-right .program-list-action {
	margin-bottom: 24px;
}

.promo-group__list-item {
	font-family: Roboto;
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 1.5;
	text-align: left;
	color: #828282;
}

.promo-group__list-item:not(:last-child) {
	margin-bottom: 5px;
}

@media (max-width: 978px) {
	.program__wrapper {
		position: sticky;
		&::before {
			display: none;
		}
		&::after {
			display: none;
		}
	}
	.program__grid {
		grid-template-columns: 1fr;
	}
	.program__column-right {
		background: #f1eafd;
		border-radius: 20px;
	}
	.program__column-left,
	.program__column-right {
		margin: 0;
	}
	.program__column-left,
	.program__column-right {
		padding: 30px;
	}
}
