.health {
	padding-top: 50px;
	padding-bottom: 50px;
}

.health__title {
	font-weight: 500;
	font-size: clamp(28px, 3.5vw, 40px);
	line-height: 1.29;
	text-align: center;
	color: #f178b6;
	margin-bottom: clamp(25px, 3.5vw, 50px);
}

.health__list {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: auto;
	gap: 30px;
}

@media (max-width: 978px) {
	.health {
		padding-top: 25px;
		padding-bottom: 25px;
	}
	.health__list {
		grid-template-columns: 1fr;
	}
}

.health__list-item {
	display: flex;
	align-items: center;
	border-radius: 18px;
	padding: 30px 7px 14px;
}

.health__list-item__icon {
	flex: 0 0 auto;
	width: 100px;
	height: 100px;
	margin-right: 6px;
}

.health__list-item__text {
	font-size: 18px;
	line-height: 1.5;
	color: #333333;
}

.health__list-item--color:nth-child(1),
.health__list-item--color:nth-child(2) {
	background: #f0e8fd;
}
.health__list-item--color:nth-child(3) {
	background: #e3f3ff;
}
