.breadcrumbs {
	padding-top: 20px;
	padding-bottom: 50px;
}
.breadcrumbs__list {
	display: flex;
	align-items: center;
}
.breadcrumbs__list-item {
	display: flex;
	align-items: center;
	color: #9a9a9a;
	&:not(:last-child) {
		margin-right: 10px;
		&::after {
			display: block;
			content: ">";
			width: 5px;
			margin-left: 10px;
			color: #9a9a9a;
		}
	}
}

.breadcrumbs__list-item:hover {
	color: #2e2e2e;
}
.breadcrumbs__list-item:hover.breadcrumbs__list-item::after {
	color: #2e2e2e;
}

.breadcrumbs__item-link {
	font-size: 14px;
	line-height: 1.2;
	color: inherit;
}

.breadcrumbs__item-link--active {
	color: #2e2e2e;
}

@media (max-width: 978px) {
	.breadcrumbs {
		padding-top: 10px;
		padding-bottom: 25px;
	}
}

@media (max-width: 768px) {
	.breadcrumbs {
		display: none;
	}
}
